/* global moment */
/**
 *
 * This file replaces the original scheduler object used by Timekit.io
 * A compilation step substitutes the original reference to a reference
 * to this object.
 *
 */
function request(method, url, data) {
  const config = { method };

  if (method.toUpperCase() === 'POST') {
    config.body = JSON.stringify(data);
  }

  return fetch(url, config)
    .then((res) => res.json())
    .then((json) => json);
}

const urlPath = path => `${window.APIGLOBAL}${path}`;

const emptyFunc = () => null;

function findTime() {
  return request('GET', urlPath('/findtime'));
}

function getUserTimezone() {
  return Promise.resolve({
    data: {
      timezone: moment.tz.guess(),
      utc_offset: moment().utcOffset(),
    },
  });
}


function Scheduler() {
  const listeners = [];

  return {
    configure: emptyFunc,
    setUser: emptyFunc,
    include: emptyFunc,
    headers: emptyFunc,
    findTime,
    getUserTimezone,
    createBooking: data => listeners.map(f => f(data))[0],
    onCreateBooking: f => listeners.push(f),
  };
}

module.exports = Scheduler;
